import { React } from 'react'
import "./BankDetails.css"

function BankDetails(props) {
    let data = JSON.parse(localStorage.getItem("particularData"))
    return (
        <div className="main container">
            <h5 className="text-center font-weight-bold pb-3"><span className="values">{data.bank_name}</span></h5>
            <p>Ifsc Code :  <span>{data.ifsc}</span></p>
            <p>Bank Id :  <span>{data.bank_id}</span></p>
            <p>Branch :  <span>{data.branch}</span></p>
            <p>City :  <span>{data.city}</span></p>
            <p>District :  <span>{data.district}</span></p>
            <p>State :  <span>{data.state}</span></p>
            <p>Full address :  <span>{data.address}</span></p>
        </div>
    )
}
export default BankDetails;